 @import url('https://fonts.googleapis.com/css?family=Montserrat');
 * {
      box-sizing: border-box;
 }
 .myProfile {
      background-color: #448283;
      font-family: Montserrat, sans-serif;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      min-height: 90%;
      margin: 0;
 }

 .img_profile{
 height:10em;
 width:10em;

 }
 h3 {
      margin: 10px 0;
 }
 h6 {
      margin: 5px 0;
      text-transform: uppercase;
 }
 p {
      font-size: 14px;
      line-height: 25px;
 }
 .card-container {
      background-color: #095D5F;
      border-radius: 5px;
      box-shadow: 0px 10px 20px -10px rgba(0,0,0,0.75);
      color: #B3B8CD;
      padding: 100px;
      width: 500px;
      max-width: 100%;
      text-align: center;
      position:absolute;
 }
 .card-container .pro {
      color: #231E39;
      background-color: #78B1DA;
      border-radius: 3px;
      font-size: 14px;
      font-weight: bold;
      padding: 3px 7px;
      position: absolute;
      z-index: 1;
      top: 30px;
      left: 30px;
 }
 .card-container .round {
      border: 1px solid #03BFCB;
      border-radius: 50%;
      padding: 7px;
 }
 button.primary {
      background-color: #03BFCB;
      border: 1px solid #03BFCB;
      border-radius: 3px;
      color: #231E39;
      font-family: Montserrat, sans-serif;
      font-weight: 500;
      padding: 10px 25px;
 }
 button.primary.ghost {
      background-color: transparent;
      color: #02899C;
 }
 .skills {
      background-color: #1F1A36;
      text-align: left;
      padding: 15px;
      margin-top: 30px;
 }
 .skills ul {
      list-style-type: none;
      margin: 0;
      padding: 0;
 }
 .skills ul li {
      border: 1px solid #2D2747;
      border-radius: 2px;
      display: inline-block;
      font-size: 12px;
      margin: 0 7px 7px 0;
      padding: 7px;
 }

 footer p {
   margin: 10px 0;
 }
 footer i {
   color: red;
 }
 footer a {
   color: #3c97bf;
   text-decoration: none;
 }